import activityList from '@/components/activityList.vue';
export default {
	data() {
		return {
			more: false,
			pageNum: 1,
			pageSize: 10,
			loading: true,
			list: []
		};
	},
	components: { activityList },
	onLoad() {
		this.getList();
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		getList() {
			if (this.loading) {
				this.loading = false;
				this.$Fetch({
					url: 'activity/getData',
					data: {
						pageNum: this.pageNum,
						pageSize: this.pageSize
					},
					debug: true,
					success: res => {
						console.log(res);
						let arr = res.lists;
						if (arr.length > 0) {
							this.list = this.list.concat(arr);
							if (arr.length === this.pageSize) {
								this.pageNum++;
								this.loading = true;
								this.more = true;
							} else {
								this.more = false;
							}
						}
					}
				});
			}
		}
	}
};
